.app-input
{
  text-align: center;
  color: blue;
}
h1{
  color: green;
}
.app-input input{
  font-size: 14px;
  border-radius: 5px;
  border: 1px solid grey;
  outline: none;
  padding: 10px 5px;
  width: 200px;
}
.app-input button
{
  background-color: rgb(0, 145, 255);
  color: white;
  border: none;
  outline: none;
  margin-top: 10px;
  padding: 10px 30px;
  font-size: 16px;
  cursor: pointer;
}
nav{
  background-color: rgb(153,47,153);
  padding: 10px 0;
  display: flex;
  justify-content: center;
  margin-top: 30px;
}
nav ul{
  list-style-type: none;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 40%;
}
nav ul li{
  cursor: pointer;
  font-size: 14px;
  font-weight: bold;
  transition: all 0.5s ease-in-out;
}
nav ul li:hover{
  transform: scale(1.4);
}
.navActive
{
  color: rgb(255, 38, 0);
}
h3{
  color: rgb(13, 0, 128);
}